.contHome{
  width: 100%;
  display: flex;
  justify-content: center;
}
.homeTextContainer{
 width: 1440px;
}

.wrapper {
  width: 1200px;
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
}

.nameText {
  margin-top: 64px;
  margin-left: 120px;
  font-size: 16px;
  color: #BEB7FB;
}

h1.pText {
  margin: 0 0 16px 120px;
  font-size: 64px;
  color: #EBEEF5;
}

.num { 
  width: 1200px;
  display: flex;
  margin-left: 36px;
  padding-top: 100px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.home {
  width: 1440px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.h3 {
  width: 1200px;
  height: 24px;
  color: rgba(118, 126, 148, 1);
  font-size: 16px;
}

@keyframes fade {
  from {
    opacity: .4;
  }
  to {
    opacity: 1;
  }
}

.cardsListContent {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.mainCardBlock {
  display: flex;
  flex-wrap: wrap;
}

.show {
  margin: 10px 12px;
  display: flex;
  flex-direction: column;
  width: 282px;
  height: 480px;
  background-color:  rgba(32, 40, 62, 0.8);
  backdrop-filter: blur(80px);
  border-radius: 12px;
  padding-top: 8px;
  overflow: hidden;
}

.star {
  color: #FFAD49;
  margin-right: 5px;

}

.span {
  color: #FFAD49;
}

a {
  display: flex;
  flex-direction: column; 
}

.containerImageShow{
  display: flex;
  width: 266px;
  height: 400px;
  border-radius: 8px;
  overflow: hidden;;
  margin-left: 8px;
}

.photo {
 width: inherit;
 height: inherit;
 transform: scale(1);
 transition: 0.3s;

}

.show:hover .photo{
  transform: scale(1.05);
  transition: 0.3s;
}

.raiting {
  width: 60px;
  height: 40px;
  margin-top: 10px;
  margin-left: 16px;
  display: flex;
  align-items: center;
  position: absolute;
  border-radius: 8px;
  background:rgba(0, 0, 0, .65);
}

.textName {
  position: relative;
  color: #EBEEF5;
  margin-left: 16px;
  margin-top: 14px;
  font-weight: 600;
}

